import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TestListComponent } from './container/test-list.component';
import { ProfessionTestComponent } from './profession-test/profession-test.component';



const routes: Routes = [
  { path: 'test-center', component: TestListComponent },
  { path: 'professtion-test', component: ProfessionTestComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TestCenterRoutingModule { }



