import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from '@shared/shared.module';


import { TestListComponent } from './container/test-list.component';
import { AnimalTestComponent } from './character-test/animal-test/animal-test.component';
import { ColorTestComponent } from './character-test/color-test/color-test.component';
import { NineHouseTestComponent } from './character-test/nine-house-test/nine-house-test.component';
import { ProfessionTestComponent } from '../test-center/profession-test/profession-test.component';

import { TestCenterRoutingModule } from './test-center-routing.module';

import { UtilsModule } from '../../utils/utils.module';
const COMPONENTS_NOROUNT = [AnimalTestComponent, ColorTestComponent, NineHouseTestComponent, ProfessionTestComponent];

@NgModule({
    imports: [
        SharedModule,
        TestCenterRoutingModule,
        UtilsModule
    ],
    declarations: [
        TestListComponent,
        ...COMPONENTS_NOROUNT
    ],
    entryComponents: COMPONENTS_NOROUNT
})
export class TestCenterModule { }



